import './app-products.css'
import ProductFirst from '../img/Product-photo-first.svg';
import ProductSecond from '../img/Product-photo-second.svg'
import ProductsThird from '../img/Product-photo-third.svg';

const AppProducts = () => {
    return (
        <div className="app-products">
            <p className="text-products">Our best</p>
            <div className="coffee-assortment">
                <div className="coffee-product ">
                    <img src={ProductFirst} alt="coffee" className="product-photo-first"/>
                    <p className="text-describe">Solimo Coffee Beans 2 kg</p>
                    <p className="product-price">10.73$</p>
                </div>
                <div className="coffee-product">
                    <img src={ProductSecond} alt="coffee" className="product-photo-second"/>
                    <p className="text-describe">Presto Coffee Beans 1 kg</p>
                    <p className="product-price">15.99$</p>            
                </div>
                <div className="coffee-product ">
                    <img src={ProductsThird} alt="coffee" className="product-photo-third"/>
                    <p className="text-describe">AROMISTICO Coffee 1 kg</p>
                    <p className="product-price">6.99$</p>
                </div>
            </div> 
        </div>
    )
}

export default AppProducts;