import { Component } from "react";

class AppInputArea extends Component {
    constructor (props) {
        super(props);
        this.state = {
            term: ''
        }
    }

    onUpdateSearch = (e) => {
        const term = e.target.value;
        this.setState({term});
        this.props.onUpdateSearch(term);
    }

    render() {
        return (
            <div className="input-area">
                <p style={{fontSize: '14px'}}>Looking for</p>
                <form style={{paddingLeft: '20px'}}>
                    <input className="input-prop" 
                    type="text" name="name" 
                    required="required" 
                    placeholder="start typing here..." 
                    style={{fontFamily: 'inherit', textAlign: 'center'}}
                    value = {this.state.term}
                    onChange = {this.onUpdateSearch}/>
                </form>
            </div>
        )
    }
}

export default AppInputArea;