import "./app-search.css";
import AppSearchItem from "./app-search-item";
import AppCountriesSearch from "./app-countries-search"
import { Component } from "react";
import AppInputArea from "./app-input-area"



class AppSearch extends Component  {

    constructor (props) {
        super(props)
        this.state = {
            currentItems: [],
            data: [
                {name: 'Solimo Coffee Beans 2 kg', country: 'Brazil', price: 10.73, id: 1},
                {name: 'AROMISTICO Coffee 2 kg', country: 'Kenya', price: 6.99, id: 2},
                {name: 'Presto Coffee Beans 1 kg', country: 'Columbia', price: 15.99, id: 3},
                {name: 'AROMISTICO Coffee 1 kg', country: 'Kenya', price: 6.99, id: 4},
                {name: 'Presto Coffee Beans 1 kg', country: 'Kenya', price: 15.99, id: 5},
                {name: 'Solimo Coffee Beans 2 kg', country: 'Columbia', price: 10.73, id: 6}
                
            ],
            term: ''
        }
        this.state.currentItems = this.state.data;
        this.chooseCountry = this.chooseCountry.bind(this)
    }

    searchProduct = (items, term) => {
        if (term.length === 0) {
            return items;
        }

        return items.filter (item =>{
            return item.name.indexOf(term) > -1
        })
    }

    chooseCountry = (country) => {
        if (country === 'all') {
            this.setState({currentItems: this.state.data})
            return
        }

        this.setState({
            currentItems: this.state.data.filter(e => e.country === country)
        })
    }

    onUpdateSearch = (term) => {
        this.setState({term});
    }


    render () {
        const {currentItems} = this.state;
        const visibleData = this.searchProduct(currentItems, this.state.term);

        const elements = visibleData.map(item => {
            const {id, ...itemProps} = item;
            return (
                <AppSearchItem key = {id} {...itemProps}/>
            )
        })
        
        return (
            <div className="search-menu">
                <div className="filter-menu">
                    <AppInputArea onUpdateSearch = {this.onUpdateSearch}/>
                    <div className="filter-area">
                        <p style={{fontSize: '14px', paddingRight: '35px'}}>Or filter</p>
                        <AppCountriesSearch chooseCountry = {this.chooseCountry}/>
                    </div>
                </div>
                <div className="choose-product">
                    <div className="product-column">
                        {elements}
                    </div>
                </div>
            </div>
        )
    }
}

export default AppSearch;