import {
    BrowserRouter as Router,
    Routes,
    Route,
} from 'react-router-dom';

import CoffeeHouse from '../app-coffee-house/app-coffee-house';
import AppOurCoffee from '../app-our-coffee/app-our-coffee';
import AppForYourPleasure from '../app-for-your-pleasure/app-for-your-pleasure';
import AppCoffeeItem from '../app-coffee-item/app-coffee-item';

import './app.css'

function App () {
    return (
        <Router>
            <div className="app">
                <Routes>
                    <Route index element={<CoffeeHouse />} />
                    <Route path='/our_coffee' element={<AppOurCoffee />} />
                    <Route path='/for_your_pleasure' element={<AppForYourPleasure />} />
                    <Route path='/coffee_item' element={<AppCoffeeItem/>} />
                </Routes>
            </div>
        </Router>
    )
}

export default App;
