import AppOurCoffeeWelcome from "../app-our-coffee-welcome/app-our-coffee-welcome";
import AppFooter from "../app-footer/app-footer";
import BlackBeans from "../img/BlackBeans.svg";
import CoffeeItem from "../img/CoffeeItem.svg";
import "./app-coffee-item.css";


const AppCoffeeItem = () => {
    return (
        <div className="">
            <AppOurCoffeeWelcome />
            <div className="content-area">
                <img src={CoffeeItem} alt="CoffeeDrink" className='coffee-drink'/>
                <div className="text-about-coffee">
                    <p className="about-beans">About it</p>
                    <div className='black-beans'>
                        <div className='black-line'></div>
                        <img src={BlackBeans} alt="black-beans"/>
                        <div className='black-line'></div>
                    </div>
                    <div className="text-item-product">
                        <p>
                            Country: Brasil
                        </p>
                        <p>
                            Description: Lorem ipsum dolor sit amet, consectetur adipiscing elit, 
                            sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut 
                            enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea 
                            commodo consequat. 
                        </p>
                        <p>
                            Price:  16.99$
                        </p>
                    </div>
                </div>
            </div>
            <AppFooter />
        </div>
    )
}

export default AppCoffeeItem;