import './header.css';
import {Link} from "react-router-dom";
import Beans from "../img/Beans.svg";


const Header = () => {
    return (
        <dl className="header">
            <dd>
                <img src= {Beans} alt ='Beans'/>
                <Link to='/' style={{textDecoration: 'none', color: '#fff'}}>Coffee house</Link>
            </dd>
            <dd>
                <Link to='/our_coffee' style={{textDecoration: 'none', color: '#fff'}}>Our coffee</Link>
            </dd>
            <dd>
                <Link to='/for_your_pleasure' style={{textDecoration: 'none', color: '#fff'}}>For your pleasure</Link>
            </dd>
        </dl>
    )
}

export default Header;