import React, { Component } from 'react';

export class AppCountriesSearch extends Component {
    constructor (props) {
        super(props) 
        this.state = {
            counries: [
                {
                    key: 'all',
                    name: 'All'
                },
                {
                    key: 'Brazil',
                    name: 'Brazil'
                },
                {
                    key: 'Kenya',
                    name: 'Kenya'
                },
                {
                    key: 'Columbia',
                    name: 'Columbia'
                },
            ]
        }
    }

    render() {
        return (
        <div>
            {this.state.counries.map(e => (
                <div className="countries" key = {e.key} onClick = {() => this.props.chooseCountry(e.key)}>{e.name}</div>
            ))}
        </div>
        )
    }
}

export default AppCountriesSearch;
