import Coffee from "../img/Coffee.svg";
import { Link } from "react-router-dom";
import './app-search.css';

const AppSearchItem = ({name, country, price}) => {
    return (
        <div>
            <Link to='/coffee_item' className="coffee-item" style={{textDecoration: 'none', color: '#000000'}}>
                
                <img src={Coffee} alt="Coffee" className="coffee-photo"/>
                <p className="text-describe">{name}</p>
                <p className="country">{country}</p>
                <p className="price">{price + '$'}</p>
            </Link>       
        </div> 
    )
}

export default AppSearchItem;