import './app-welcome.css';
import WhiteBeans from '../img/WhiteBeans.svg';
import Header from '../app-header/header';


const AppWelcome = () => {
    return (
        <div className="app-greetings">
            <Header />
            <p className="text-greetings">Everything You Love About Coffee</p>
            <div className='white-beans'>
                <div className='white-line'></div>
                <img src={WhiteBeans} alt="white-beans"/>
                <div className='white-line'></div>
            </div>
            <p className="text">We makes every day full of energy and taste</p>
            <p className="text-question">Want to try our beans?</p>
        </div>
    )
}

export default AppWelcome;