import './app-footer.css';
import WhiteBlackBeans from '../img/White-black-beans.svg';
import BlackBeans from '../img/BlackBeans.svg';
import { Link } from 'react-router-dom';

const AppFooter = () => {
    return (
        <footer className="footer">
            <dl className="footer-end">
                <dd>
                    <img src= {WhiteBlackBeans} alt ='Beans'/>
                    <Link to='/' style={{textDecoration: 'none', color: '#000000'}}>Coffee house</Link>
                </dd>
                <dd>
                    <Link to='/our_coffee' style={{textDecoration: 'none', color: '#000000'}}>Our coffee</Link>
                </dd>
                <dd>
                    <Link to='/for_your_pleasure' style={{textDecoration: 'none', color: '#000000'}}>For your pleasure</Link>
                </dd>
            </dl>
            <div className='black-beans'>
                <div className='black-line'></div>
                <img src={BlackBeans} alt="black-beans"/>
                <div className='black-line'></div>
            </div>
        </footer>
    )
}

export default AppFooter;